import React from 'react';
export default class CallbackSection extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <section style={{marginLeft : "2.5%",marginRight:"2.5%"}}>
                <div className="py-3 py-md-4 py-lg-5">
                    <h3 className="text-center">
                        Get a Callback
                    </h3>
                    <p className="text-center mb-3 mb-lg-5">
                        Provide us with your name and phone number to get an instant callback from one of our healthcare consultants.
                    </p>

                    <center>
                    <form action="#" method="post">
                        <input type="hidden" name="_token" />    <div className="row">
                            <div className="col-md-6 col-lg-4">
                                <div className="form-group mb-lg-0">
                                    
                                    <input className="form-control form-control-lg " id="callback-name" name="name" placeholder="Name" value=""/>
                                                </div>
                            </div>
                            <div className="col-md-6 col-lg-4">
                                <div className="form-group mb-lg-0">
                                    
                                    <input className="form-control form-control-lg " id="callback-phone" name="phone" placeholder="Phone number" required="" type="tel" value=""/>
                                                </div>
                            </div>
                            <div className="col-md-6 col-lg-4">
                                <button className="btn btn-primary btn-lg btn-block">
                                    <i className="fas fa-phone-alt mr-1"></i> Request Callback
                                </button>
                            </div>
                        </div>
                    </form>
                    </center>

                </div>
            </section>
        )
    }
}