import React from 'react';
export default class Form extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <div >
                <ul className="list-group list-group-flush" style={{borderRadius:"100px"}}>
                    <li className="list-group-item">
                    <p >Submit your requirement with us and speak to one of our expert healthcare consultants.</p>
                
                    </li>
                    <li className="list-group-item">
                        <form>
                            <div className="mb-3">
                                <label className="form-label">Name</label>
                                <input className="form-control"  aria-describedby="nameHelp" required={true}/>
                                </div>
                            <div className="mb-3">
                                <label className="form-label">Phone Number</label>
                                <input  className="form-control" required={true}/>
                                <div className="form-text">We'll never share your phone number with anyone else.</div>
                            </div>
                            <div className="mb-3 form-check">
                                <input type="radio" className="form-check-input"  disabled={true}/>
                                <label className="form-check-label">Attendent</label>
                            </div>

                            <div className="mb-3 form-check">
                                <input type="radio" className="form-check-input" />
                                <label className="form-check-label" >Nurse</label>
                            </div>

                            <div className="mb-3 form-check">
                                <input type="radio" className="form-check-input"  disabled={true}/>
                                <label className="form-check-label" >Physiologist</label>
                            </div>
                            <button type="submit" className="btn btn-primary">Submit</button>
                        </form>
                
                </li>
                </ul>
            </div>
        )
    }
}