import React from 'react';
export default class Service extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return(
            <div className="col-sm-6 col-lg-3">
                    <div className="card border-primary mb-3 mb-lg-5">
                        <div className="card-body text-center">
                            <p className="card-text">
                                <img width = "128" height = "128"alt={this.props.data.serviceName} src={this.props.data.serviceImageUrl}/>
                            </p>
                            <h5 className="card-title"><a className="text-secondary" data-toggle="modal" href="#modal-callback">{this.props.data.serviceName}</a></h5>
                            <p className="card-text">{this.props.data.serviceDescription}</p>
                        </div>
                    </div>
                </div>
        )
    }
}